import React, {
  ReactNode, useCallback, useMemo, useState,
} from 'react';
import isHotkey from 'is-hotkey';
import {
  Editable, withReact, useSlate, Slate, RenderLeafProps, RenderElementProps,
} from 'slate-react';
import {
  Editor,
  Transforms,
  createEditor,
  Descendant,
  Element as SlateElement,
} from 'slate';
import { withHistory } from 'slate-history';
import {
  Divider,
  HStack, Icon, IconButton,
} from '@chakra-ui/react';
import {
  GoBold, GoCode, GoItalic, GoListOrdered, GoListUnordered, GoQuote,
} from 'react-icons/go';
import { MdFormatUnderlined } from 'react-icons/md';
import { IconType } from 'react-icons/lib';
import { FaHeading } from 'react-icons/fa';
import { CgFormatHeading } from 'react-icons/cg';
import { CustomTextMark, CustomElementType } from '../types/slate';

const HOTKEYS: Record<string, CustomTextMark> = {
  'mod+b': 'bold',
  'mod+i': 'italic',
  'mod+u': 'underline',
  'mod+`': 'code',
};

const LIST_TYPES = ['numbered-list', 'bulleted-list'];

const isBlockActive = (editor: Editor, format: CustomElementType) => {
  const [match] = Editor.nodes(editor, {
    match: (n) => !Editor.isEditor(n) && SlateElement.isElement(n) && n.type === format,
  });

  return !!match;
};

const isMarkActive = (editor: Editor, format: CustomTextMark) => {
  const marks = Editor.marks(editor);
  return marks ? marks[format] === true : false;
};

const toggleBlock = (editor: Editor, format: CustomElementType) => {
  const isActive = isBlockActive(editor, format);
  const isList = LIST_TYPES.includes(format);

  Transforms.unwrapNodes(editor, {
    match: (n) => LIST_TYPES.includes(
      String(!Editor.isEditor(n) && SlateElement.isElement(n) && n.type),
    ),
    split: true,
  });
  let type: CustomElementType;

  if (isActive) {
    type = 'paragraph';
  } else if (isList) {
    type = 'list-item';
  } else {
    type = format;
  }
  const newProperties: Partial<SlateElement> = {
    type,
  };
  Transforms.setNodes(editor, newProperties);

  if (!isActive && isList) {
    const block = { type: format, children: [] };
    Transforms.wrapNodes(editor, block);
  }
};

const toggleMark = (editor: Editor, format: CustomTextMark) => {
  const isActive = isMarkActive(editor, format);

  if (isActive) {
    Editor.removeMark(editor, format);
  } else {
    Editor.addMark(editor, format, true);
  }
};

const Element = ({ attributes, children, element }: RenderElementProps) => {
  switch (element.type) {
    case 'block-quote':
      return <blockquote {...attributes}>{children}</blockquote>;
    case 'bulleted-list':
      return <ul {...attributes}>{children}</ul>;
    case 'heading-one':
      return <h1 {...attributes}>{children}</h1>;
    case 'heading-two':
      return <h2 {...attributes}>{children}</h2>;
    case 'list-item':
      return <li {...attributes}>{children}</li>;
    case 'numbered-list':
      return <ol {...attributes}>{children}</ol>;
    default:
      return <p {...attributes}>{children}</p>;
  }
};

const Leaf = ({ attributes, children, leaf }: RenderLeafProps) => {
  let modChildren: ReactNode = children;
  if (leaf.bold) {
    modChildren = <strong {...attributes}>{children}</strong>;
  }

  if (leaf.code) {
    modChildren = <code {...attributes}>{children}</code>;
  }

  if (leaf.italic) {
    modChildren = <em {...attributes}>{children}</em>;
  }

  if (leaf.underline) {
    modChildren = <u {...attributes}>{children}</u>;
  }

  return <span {...attributes}>{modChildren}</span>;
};

const BlockButton = ({ format, icon }: { format: CustomElementType, icon: IconType }) => {
  const editor = useSlate();
  const isActive = isBlockActive(editor, format);
  return (
    <IconButton
      aria-label={format}
      icon={<Icon as={icon} color={isActive ? undefined : 'gray.300'} />}
      variant="outline"
      onClick={(event) => {
        event.preventDefault();
        toggleBlock(editor, format);
      }}
    />
  );
};

const MarkButton = ({ format, icon }: { format: CustomTextMark, icon: IconType }) => {
  const editor = useSlate();
  const isActive = isMarkActive(editor, format);
  return (
    <IconButton
      aria-label={format}
      icon={<Icon as={icon} color={isActive ? undefined : 'gray.300'} />}
      variant="outline"
      onClick={(event) => {
        event.preventDefault();
        toggleMark(editor, format);
      }}
    />
  );
};

const initialValue: Descendant[] = [
  {
    type: 'paragraph',
    children: [
      { text: 'This is editable ' },
      { text: 'rich', bold: true },
      { text: ' text, ' },
      { text: 'much', italic: true },
      { text: ' better than a ' },
      { text: '<textarea>', code: true },
      { text: '!' },
    ],
  },
  {
    type: 'paragraph',
    children: [
      {
        text:
          "Since it's rich text, you can do things like turn a selection of text ",
      },
      { text: 'bold', bold: true },
      {
        text:
          ', or add a semantically rendered block quote in the middle of the page, like this:',
      },
    ],
  },
  {
    type: 'block-quote',
    children: [{ text: 'A wise quote.' }],
  },
  {
    type: 'paragraph',
    children: [{ text: 'Try it out for yourself!' }],
  },
];

const SlateEditor = () => {
  const [value, setValue] = useState<Descendant[]>(initialValue);
  const renderElement = useCallback((props: RenderElementProps) => <Element {...props} />, []);
  const renderLeaf = useCallback((props: RenderLeafProps) => <Leaf {...props} />, []);
  const editor = useMemo(() => withHistory(withReact(createEditor())), []);

  return (
    <Slate editor={editor} value={value} onChange={(v) => setValue(v)}>
      <HStack>
        <MarkButton format="bold" icon={GoBold} />
        <MarkButton format="italic" icon={GoItalic} />
        <MarkButton format="underline" icon={MdFormatUnderlined} />
        <MarkButton format="code" icon={GoCode} />
        <BlockButton format="heading-one" icon={FaHeading} />
        <BlockButton format="heading-two" icon={CgFormatHeading} />
        <BlockButton format="block-quote" icon={GoQuote} />
        <BlockButton format="numbered-list" icon={GoListOrdered} />
        <BlockButton format="bulleted-list" icon={GoListUnordered} />
      </HStack>
      <Divider mt={3} mb={3} />
      <Editable
        renderElement={renderElement}
        renderLeaf={renderLeaf}
        placeholder="Enter some rich text"
        spellCheck
        autoFocus
        onKeyDown={(event) => {
          Object.entries(HOTKEYS).forEach(([hotkey, mark]) => {
            if (isHotkey(hotkey, event)) {
              event.preventDefault();
              toggleMark(editor, mark);
            }
          });
        }}
      />
    </Slate>
  );
};

export default SlateEditor;
