import {
  Box,
  Button, HStack, Icon, StackDivider, VStack,
} from '@chakra-ui/react';
import React from 'react';
import { GiDiceTwentyFacesTwenty } from 'react-icons/gi';
import { useHistory } from 'react-router-dom';
import { Character } from '../Characters/Character.interfaces';
import { NPC } from '../Characters/NPC.interfaces';
import { Player } from '../Characters/Player.interfaces';
import { useAppDispatch, useAppSelector } from '../common/hooks';
import { setTurn } from '../common/campaignSlice';
import { rollAllPlayerInitiative } from '../common/playerSlice';
import { NPCTurn } from './NPCTurn';
import './Initiative.css';
import { PlayerTurn } from './PlayerTurn';
import { useQuery } from '../common/util';

type CharacterType = 'player' | 'npc';

type SortedTurnsArray<T = CharacterType> = Array<{
  character: Character;
  type: T;
}>

export const getSortedTurns = (
  {
    players,
    npcs,
    showAllTurns,
    currentTurnId,
  } : {
    players: Player[];
    npcs: NPC[];
    showAllTurns?: boolean;
    currentTurnId?: string;
  },
) => {
  const formattedPlayers: SortedTurnsArray<'player'> = players.map((i) => ({
    character: i,
    type: 'player',
  }));

  const formattedNPCs: SortedTurnsArray<'npc'> = npcs.map((i) => ({
    character: i,
    type: 'npc',
  }));

  const sortedTurns: SortedTurnsArray = [
    ...formattedPlayers,
    ...formattedNPCs,
  ]
    .filter((i) => showAllTurns || i.character.displayOnTurnOrder)
    .sort((a, b) => {
      const aChar = a.character;
      const bChar = b.character;
      return (bChar.initiative + bChar.initiativeMod) - (aChar.initiative + aChar.initiativeMod);
    });

  const returnValue = (array: SortedTurnsArray): Array<{
    isActive: boolean;
    character: Character;
    type: CharacterType
  }> => array.map((i, idx) => ({
    isActive: idx === 0,
    character: i.character,
    type: i.type,
  }));

  if (!currentTurnId) {
    return returnValue(sortedTurns);
  }

  const currentTurnIndex = sortedTurns.findIndex((i) => i.character.id === currentTurnId) || 0;
  const getSortedByNextActiveTurn = () => [
    ...sortedTurns
      .slice(currentTurnIndex + 1, sortedTurns.length),
    ...sortedTurns
      .slice(0, currentTurnIndex + 1),
  ];

  return returnValue(getSortedByNextActiveTurn());
};

export function Initiative() {
  const history = useHistory();
  const query = useQuery();
  const tinyMode = query.get('tinymode');

  const changePageMethod = ({ type, id }: {
    type: string;
    id: string
  }): void => {
    const historyObj = tinyMode ? (window.opener as any)?.appHistory as typeof history : history;
    if (!historyObj) return;
    historyObj.push(`/${type}/${id}`);
    const maxAttempt = 20;
    const findFocus = (iter = 0) => {
      if (iter >= maxAttempt) return;
      const element = document.querySelector(`#currentHP-${id}`) as HTMLInputElement | null;

      if (!element) {
        setTimeout(() => {
          findFocus(iter + 1);
        }, 50);
      } else {
        element.focus();
      }
    };
    findFocus();
  };

  const players = useAppSelector<Player[]>((state) => state.players.players);
  const npcs = useAppSelector<NPC[]>((state) => state.npcs.npcs);
  const currentTurnId = useAppSelector<string>((state) => state.campaign.currentTurnId);

  const dispatch = useAppDispatch();

  const turns = getSortedTurns({
    players,
    npcs,
    showAllTurns: false,
    currentTurnId,
  });

  const rollInitiativesAction = () => {
    dispatch(rollAllPlayerInitiative());
    dispatch(setTurn(''));
  };

  const setTurnAction = () => {
    if (turns.length < 1) {
      return;
    }

    if (!currentTurnId) {
      dispatch(setTurn(turns[0].character.id));
      return;
    }

    const currentTurnCharacter = turns.findIndex((i) => i.character.id === currentTurnId);

    if (!currentTurnCharacter) {
      dispatch(setTurn(turns[1].character.id));
      return;
    }
    const nextCharacter = turns[(currentTurnCharacter + 1) % turns.length];
    dispatch(setTurn(nextCharacter.character.id));
  };

  return (
    <Box>
      <VStack
        divider={tinyMode ? undefined : <StackDivider color="gray.200" />}
        mb={tinyMode ? 0 : 2}
        spacing={tinyMode ? 0 : 2}
      >
        {turns.map(({ character, isActive, type }) => {
          if (type === 'player') {
            const player = character as Player;
            return (
              <PlayerTurn
                onClick={() => changePageMethod({ type: 'players', id: character.id })}
                isActive={isActive}
                player={player}
                key={character.id}
                tinyMode={tinyMode}
              />
            );
          } if (type === 'npc') {
            const npc = character as NPC;
            return (
              <NPCTurn
                onClick={() => changePageMethod({
                  type: npc.isHostile ? 'enemies' : 'npcs',
                  id: character.id,
                })}
                isActive={isActive}
                npc={npc}
                key={character.id}
                tinyMode={tinyMode}
              />
            );
          }

          return false;
        })}
        ;
      </VStack>
      <HStack
        position="sticky"
        backgroundColor="white"
        bottom="0"
        width="100%"
      >
        <Button
          onClick={rollInitiativesAction}
          width="100%"
          size={tinyMode ? 'sm' : 'md'}
          variant="outline"
        >
          Roll Initiative
          <Icon as={GiDiceTwentyFacesTwenty} />
        </Button>
        <Button
          onClick={setTurnAction}
          width="100%"
          size={tinyMode ? 'sm' : 'md'}
          variant="outline"
        >
          Next Turn
        </Button>
        { !tinyMode && (
          <Button
            width="100%"
            variant="outline"
            size={tinyMode ? 'sm' : 'md'}
            onClick={() => window.open('/dndtracker/?tinymode=true', 'dndtracker_turns', 'resizable,width=500,height=300')}
          >
            Display in Compact Mode
          </Button>
        )}
      </HStack>
    </Box>
  );
}
