import { AddIcon, CloseIcon } from '@chakra-ui/icons';
import {
  AccordionButton, AccordionIcon, AccordionItem, Flex,
  AccordionPanel, Box, HStack, Icon, IconButton, Input, VStack, Text, Spacer, InputProps,
} from '@chakra-ui/react';
import { FieldArray, useFormikContext } from 'formik';
import React from 'react';
import { AiOutlineLink } from 'react-icons/ai';
import { Character, ExternalNote } from './Character.interfaces';

export const ModifyExternalNoteReferences = ({ character }: { character: Character }) => {
  const context = useFormikContext<Character>();
  const errors = context?.errors;
  return (
    <AccordionItem>
      <AccordionButton>
        <Box flex="1" textAlign="left">
          <strong>External Notes</strong>
        </Box>
        <AccordionIcon />
      </AccordionButton>
      <AccordionPanel>
        <FieldArray
          name="externalNotes"
          render={(arrayHelpers) => {
            const linkLabelId = `link-label-${character.id}`;
            const linkUrlId = `link-url-${character.id}`;
            const addNote = () => {
              const linkLabelEle = document.querySelector(`#${linkLabelId}`) as InputProps;
              const linkUrlEle = document.querySelector(`#${linkUrlId}`) as InputProps;

              if (linkLabelEle?.value && linkUrlEle?.value) {
                const labelInput = linkLabelEle.value.toString();
                const urlInput = linkUrlEle.value.toString();
                linkLabelEle.value = '';
                linkUrlEle.value = '';

                const value: ExternalNote = {
                  type: 'link',
                  label: labelInput,
                  url: urlInput,
                };

                arrayHelpers.push(value);
              }
            };
            return (
              <VStack>
                {
                context.values.externalNotes.map((note, idx) => (
                  <Flex
                    alignItems="center"
                    width="100%"
                    border="0.2em solid #0000FF22"
                    borderRadius={6}
                    cursor="pointer"
                    _hover={{
                      border: '0.2em solid #0000FF',
                    }}
                    transition="0.2s"
                    key={note.label + note.url}
                  >
                    <Box
                      p={1.5}
                      onClick={() => window.open(note.url, '_blank')}
                    >
                      <Icon as={AiOutlineLink} w={6} h={6} color="blue" />
                    </Box>
                    <Text
                      color="blue"
                      textOverflow="ellipsis"
                      overflow="hidden"
                      whiteSpace="nowrap"
                      onClick={() => window.open(note.url, '_blank')}
                      width="100%"
                    >
                      {note.label}
                      {' '}
                      (
                      {note.url}
                      )
                    </Text>
                    <Spacer />
                    <IconButton
                      ml={1}
                      aria-label="Save"
                      icon={<Icon as={CloseIcon} />}
                      color="red"
                      variant="ghost"
                      onClick={() => arrayHelpers.remove(idx)}
                    />
                  </Flex>
                ))
              }
                <HStack width="100%">
                  <Input
                    placeholder="Link Label"
                    id={linkLabelId}
                  />
                  <Input
                    placeholder="URL"
                    id={linkUrlId}
                  />
                  <IconButton
                    ml={2}
                    aria-label="Save"
                    icon={<Icon as={AddIcon} />}
                    colorScheme="blue"
                    variant="outline"
                    onClick={() => addNote()}
                  />
                </HStack>
                <Text
                  width="100%"
                  color="red"
                  hidden={!(!!errors.externalNotes && errors.externalNotes.length > 0)}
                >
                  There are errors with the links inserted.
                  Make sure the URLs are of a valid format.
                </Text>
              </VStack>
            );
          }}
        />
      </AccordionPanel>
    </AccordionItem>
  );
};
