import {
  Badge, Box, Grid, HStack, Icon, Text,
} from '@chakra-ui/react';
import React from 'react';
import { BsHeartFill } from 'react-icons/bs';
import { FaHeartBroken } from 'react-icons/fa';
import {
  GiCheckedShield, GiDiceTwentyFacesTwenty, GiWalkingBoot,
} from 'react-icons/gi';
import { characterTypeColours } from '../App';
import { NPC } from '../Characters/NPC.interfaces';

interface NPCTurnProps {
  npc: NPC;
  isActive: boolean;
  tinyMode: string | null;
  onClick: () => void;
}

export function NPCTurn({
  npc, isActive, tinyMode, onClick,
}: NPCTurnProps) {
  const tempHP = npc.tempHP ? ` (${npc.tempHP})` : '';

  let formColour = characterTypeColours.neutral;

  if (npc.isHostile) {
    formColour = characterTypeColours.enemy;
  } else if (npc.isFriendly) {
    formColour = characterTypeColours.friendly;
  }

  return (
    <Box
      className="turn-block turn-block-npc"
      p={tinyMode ? '0.1em' : '0.5em'}
      border={`${isActive && !tinyMode ? '0.4em' : '0.2em'} solid ${formColour}${isActive ? 'FF' : 22}`}
      borderRadius="0.4em"
      opacity={isActive ? '100%' : '80%'}
      cursor="pointer"
      onClick={onClick}
    >
      <Grid templateColumns="repeat(4, 1fr)" fontSize={tinyMode ? 'sm' : 'md'}>
        <Text fontWeight="bold">{npc.showName ? npc.name : '???'}</Text>
        {
          npc.showAC
            ? (
              <Text>
                <Icon as={GiCheckedShield} />
                {' '}
                {npc.armourClass}
              </Text>
            )
            : <Text />
        }
        {
          npc.showHealth
            ? (
              <Text>
                <Icon as={BsHeartFill} />
                {' '}
                {npc.currentHP}
                {' '}
                {tempHP}
                {' '}
                /
                {' '}
                {npc.maxHP}
              </Text>
            )
            : (
              <Text color="red.800">
                <Icon as={FaHeartBroken} />
                {' '}
                {Math.max(npc.maxHP - npc.currentHP, 0)}
              </Text>
            )
        }
        {
          npc.showInitiative
            ? (
              <Text>
                <Icon as={GiWalkingBoot} />
                {' '}
                {npc.initiative + npc.initiativeMod}
                {' '}
                (
                {npc.initiative}
                <Icon as={GiDiceTwentyFacesTwenty} />
                {' '}
                {npc.initiativeMod >= 0 ? '+' : '-'}
                {' '}
                {Math.abs(npc.initiativeMod)}
                )
              </Text>
            )
            : <Text />
        }
      </Grid>
      {
        npc.conditions.length > 0 && (
        <HStack>
          {npc.conditions.map((i) => (
            <Badge
              variant="solid"
              key={i}
            >
              {i}
            </Badge>
          ))}
        </HStack>
        )
      }
    </Box>
  );
}
