import {
  Box,
  Center, Container, Divider, Heading,
} from '@chakra-ui/react';
import {
  Route, Switch, useHistory,
} from 'react-router-dom';
import React from 'react';
import { setCampaignTitle, setTurn } from '../common/campaignSlice';
import { useAppDispatch } from '../common/hooks';
import { refreshNPCs } from '../common/npcSlice';
import { refreshPlayers } from '../common/playerSlice';
import { loadFromLocalStorage, RootState } from '../common/store';
import { CampaignTitle } from './CampaignTitle';
import { Sidebar } from './Sidebar';
import './App.css';
import RouteConfig from './RouteConfig';
import { useQuery } from '../common/util';

export function App() {
  const dispatch = useAppDispatch();
  const history = useHistory();
  const query = useQuery();

  (window as any).appHistory = history;

  const tinyMode = query.get('tinymode');
  const [appMinW, appMaxW] = tinyMode ? [400, 800] : [650, 800];

  window.addEventListener('storage', () => {
    const persistedStorage: RootState = loadFromLocalStorage();

    if (!document.hasFocus()) {
      const refreshActions = [
        refreshPlayers(persistedStorage?.players?.players || []),
        refreshNPCs(persistedStorage?.npcs?.npcs || []),
        setCampaignTitle(persistedStorage?.campaign?.campaignTitle || ''),
        setTurn(persistedStorage?.campaign?.currentTurnId || ''),
      ];
      refreshActions.map(dispatch);
    }
  });

  return (
    <Box
      className="App"
      pt={tinyMode ? '0' : '2em'}
    >
      {!tinyMode && <Sidebar routes={RouteConfig} />}
      <Container maxW={appMaxW} minW={appMinW}>
        {!tinyMode && <CampaignTitle />}
        <Switch>
          {
            RouteConfig.map(({
              component, basePath, pageTitle, exact,
            }) => (
              <Route
                path={basePath}
                exact={exact}
                key={pageTitle}
              >
                {
                  !tinyMode && (
                    <Box>
                      <Center><Heading size="md">{pageTitle}</Heading></Center>
                      <Divider mt={4} mb={4} />
                    </Box>
                  )
                }
                {component}
              </Route>
            ))
          }
        </Switch>
      </Container>
    </Box>
  );
}
