import {
  Center, Divider, Icon, IconButton,
} from '@chakra-ui/react';
import React from 'react';
import { RiSkull2Fill, RiSkull2Line } from 'react-icons/ri';
import { HiPlus, HiPlusCircle } from 'react-icons/hi';
// import { TiPlus, TiPlusOutline } from 'react-icons/ti';
import { Player } from '../Characters/Player.interfaces';
import { useAppDispatch } from '../common/hooks';
import { modifyPlayer } from '../common/playerSlice';

interface DeathSaveCounterProps {
  player: Player;
  size?: 'xs' | 'sm' | 'md' | 'lg';
  disableAction?: boolean;
}

export const DeathSaveCounter = ({ player, size, disableAction }: DeathSaveCounterProps) => {
  const dispatch = useAppDispatch();

  const setDeathSaveThrowAction = (count: number, type: 'pass' | 'fail') => {
    if (disableAction) return;
    let prop: keyof Player = 'failedDeathSaves';

    if (type === 'pass') {
      prop = 'passedDeathSaves';
    }
    dispatch(modifyPlayer({
      id: player.id,
      [prop]: count,
    }));
  };

  const DeathSaveFailedIcon = (saveCount: number) => (
    player.failedDeathSaves >= saveCount
      ? <Icon as={RiSkull2Fill} color="darkred" />
      : <Icon as={RiSkull2Line} />
  );
  const DeathSaveSuccessIcon = (saveCount: number) => (
    player.passedDeathSaves >= saveCount
      ? <Icon as={HiPlusCircle} color="green" />
      : <Icon as={HiPlus} />
  );

  const numberArray = [3, 2, 1];
  const reversedNumberArray = [1, 2, 3];

  return (
    <Center>
      {
        numberArray.map((saveCount) => (
          <IconButton
            size={size || 'xs'}
            variant="ghost"
            aria-label="Death Save Failed"
            key={`failed-save-${saveCount}`}
            icon={DeathSaveFailedIcon(saveCount)}
            cursor={disableAction ? 'default' : 'auto'}
            pointerEvents={disableAction ? 'none' : 'auto'}
            onClick={() => {
              if (player.failedDeathSaves === saveCount) {
                return setDeathSaveThrowAction(Math.max(saveCount - 1, 0), 'fail');
              }
              return setDeathSaveThrowAction(Math.max(saveCount, 0), 'fail');
            }}
            hidden={disableAction && player.failedDeathSaves < saveCount}
            minW={!disableAction ? '24px' : 0}
          />
        ))
      }
      <Divider orientation="vertical" />
      {
        reversedNumberArray.map((saveCount) => (
          <IconButton
            size={size || 'xs'}
            variant="ghost"
            aria-label="Death Save Suceeded"
            key={`passed-save-${saveCount}`}
            icon={DeathSaveSuccessIcon(saveCount)}
            cursor={disableAction ? 'default' : 'auto'}
            pointerEvents={disableAction ? 'none' : 'auto'}
            onClick={() => {
              if (player.passedDeathSaves === saveCount) {
                return setDeathSaveThrowAction(Math.max(saveCount - 1, 0), 'pass');
              }
              return setDeathSaveThrowAction(Math.max(saveCount, 0), 'pass');
            }}
            minW={!disableAction ? '24px' : 0}
            hidden={disableAction && player.passedDeathSaves < saveCount}
          />
        ))
      }
    </Center>
  );
};
