import { Box, HStack, Text } from '@chakra-ui/react';
import { useFormikContext } from 'formik';
import React from 'react';
import { NumberInputControl } from '../common/formik-chakra';
import { Character } from './Character.interfaces';

export const ModifyCurrentHPInput = ({ character }: { character: Character }) => {
  const context = useFormikContext<Character>();
  return (
    <>
      <Box>
        <HStack>
          <Text fontWeight="bold">THP </Text>
          <NumberInputControl
            hideError
            name="tempHP"
            width="6em"
            onKeyPress={(e) => e.code === 'Enter' && context.submitForm()}
          />
        </HStack>
      </Box>
      <Box>
        <HStack>
          <Text fontWeight="bold">HP </Text>
          <NumberInputControl
            hideError
            name="currentHP"
            width="6em"
            fieldId={`currentHP-${character.id}`}
            onKeyPress={(e) => e.code === 'Enter' && context.submitForm()}
          />
          <Text>
            /
            {' '}
            {character.maxHP}
          </Text>
        </HStack>
      </Box>
    </>
  );
};
