import {
  VStack,
} from '@chakra-ui/react';
import React from 'react';
import { useParams } from 'react-router-dom';
import { useAppSelector } from '../common/hooks';
import { AddNPCForm } from './AddNPCForm';
import { ModifyNPCForm } from './ModifyNPCForm';
import { NPC } from './NPC.interfaces';

const NPCFormList = ({ npcs }: { npcs: NPC[] }) => (
  <VStack>
    {
        npcs.map((npc) => (
          <ModifyNPCForm
            key={npc.id}
            npc={npc}
          />
        ))
      }
  </VStack>
);

interface NPCsProps {
  showHostile?: boolean;
  showFriendly?: boolean;
  showNeutral?: boolean;
}

export function NPCs({
  showHostile, showFriendly, showNeutral,
}: NPCsProps) {
  const params = useParams<{ id?: string }>();
  const npcs = useAppSelector((state) => state.npcs.npcs
    .filter((i) => {
      if (showHostile && i.isHostile) {
        return true;
      }
      if (showFriendly && i.isFriendly) {
        return true;
      }
      if (showNeutral && (!i.isFriendly && !i.isHostile)) {
        return true;
      }
      return false;
    })
    .filter((i) => {
      if (params.id) {
        return i.id === params.id;
      }
      return true;
    }));

  return (
    <>
      {!params.id && <AddNPCForm showHostile={showHostile} showFriendly={showFriendly} />}
      <NPCFormList npcs={npcs} />
    </>
  );
}
