import { Character } from './Character.interfaces';

export const resetManualFormFields = (character: Character) => {
  const linkLabelId = `#link-label-${character.id}`;
  const linkUrlId = `#link-url-${character.id}`;
  const tagInputId = `#tag-input-${character.id}`;
  const ids = [
    linkLabelId,
    linkUrlId,
    tagInputId,
  ];
  ids.forEach((i) => {
    const element = (document.querySelector(i) as HTMLInputElement);
    if (element) {
      element.value = '';
    }
  });
};
