import { AddIcon } from '@chakra-ui/icons';
import {
  Button, HStack, Icon, FormControl, FormLabel, Spacer, SimpleGrid, VStack,
  Text,
} from '@chakra-ui/react';
import { Form, Formik, useFormikContext } from 'formik';
import React from 'react';
import { GiDiceTwentyFacesTwenty } from 'react-icons/gi';
import * as Yup from 'yup';
import { InputControl, NumberInputControl, SwitchControl } from '../common/formik-chakra';
import { useAppDispatch } from '../common/hooks';
import { addNPC } from '../common/npcSlice';
import { Dice, rollDice } from '../common/util';
import { NPC } from './NPC.interfaces';

interface NPCsProps {
  showHostile?: boolean;
  showFriendly?: boolean;
}

type AddFormFields = Array<
    [string, keyof NPC, typeof InputControl | typeof NumberInputControl]
  >;

function AddNPCFlags() {
  const { setFieldValue, values } = useFormikContext<NPC & { rollInitiativeOnCreate: boolean }>();
  return (
    <VStack
      mt={4}
      mb={4}
    >
      <SimpleGrid
        mt={4}
        mb={4}
        fontSize="sm"
        templateColumns="repeat(3, 1fr)"
        spacing={4}
        width="100%"
      >
        {
          ([
            ['Show on Turn Order', 'displayOnTurnOrder'],
            ['Show name', 'showName'],
            ['Show health', 'showHealth'],
            ['Show AC', 'showAC'],
            ['Show initiative', 'showInitiative'],
            ['Roll initiative on creation', 'rollInitiativeOnCreate'],
          ]).map(([displayString, key]) => (
            <FormControl
              display="flex"
              alignItems="center"
              key={key}
            >
              <FormLabel mb="0" fontSize="xs">
                {displayString}
              </FormLabel>
              <Spacer />
              <SwitchControl name={key} />
            </FormControl>
          ))
        }
      </SimpleGrid>
      {
        !values.rollInitiativeOnCreate
        && (
          <HStack
            width="100%"
          >
            <Button
              width="100%"
              onClick={() => setFieldValue('initiative', rollDice(Dice.d20))}
              variant="outline"
              rightIcon={<Icon as={GiDiceTwentyFacesTwenty} />}
            >
              Roll Initiative
            </Button>
            <HStack>
              <Text>Initiative</Text>
              <NumberInputControl hideError name="initiative" width="8em" />
            </HStack>
          </HStack>
        )
      }
    </VStack>
  );
}

export function AddNPCForm({ showFriendly, showHostile }: NPCsProps) {
  const dispatch = useAppDispatch();

  const addNPCValidationSchema = Yup.object({
    name: Yup.string().required().label('Name'),
    maxHP: Yup.number().integer().moreThan(0).required()
      .label('Maximum HP'),
    initiativeMod: Yup.number().integer().required().label('Initiative Modifier'),
    armourClass: Yup.number().integer().moreThan(0).default(10)
      .label('Armour Class'),
    initiative: Yup.number().integer().min(1).max(20),

    rollInitiativeOnCreate: Yup.boolean(),

    displayOnTurnOrder: Yup.boolean(),
    showHealth: Yup.boolean(),
    showInitiative: Yup.boolean(),
    showAC: Yup.boolean(),
    showName: Yup.boolean(),
  });

  const addFormFields: AddFormFields = [
    ['NPC Name', 'name', InputControl],
    ['Maximum HP', 'maxHP', NumberInputControl],
    ['Initiative Modifier', 'initiativeMod', NumberInputControl],
    ['Armour Class', 'armourClass', NumberInputControl],
  ];
  return (
    <Formik
      initialValues={{
        name: '',
        maxHP: 0,
        initiativeMod: 0,
        armourClass: 0,
        initiative: 1,

        rollInitiativeOnCreate: true,

        displayOnTurnOrder: true,
        showHealth: false,
        showInitiative: false,
        showAC: false,
        showName: false,
      }}
      validationSchema={addNPCValidationSchema}
      onSubmit={
          (values, actions) => {
            const initiativeValue = values.rollInitiativeOnCreate
              ? rollDice(Dice.d20) : values.initiative;
            actions.setFieldValue('initiative', initiativeValue);
            dispatch(addNPC({
              name: values.name,
              maxHP: values.maxHP,
              currentHP: values.maxHP,
              tempHP: 0,
              initiative: initiativeValue,
              initiativeMod: values.initiativeMod,
              armourClass: values.armourClass,
              conditions: [],
              displayOnTurnOrder: values.displayOnTurnOrder,
              notes: '',
              externalNotes: [],
              isFriendly: showFriendly || false,
              isHostile: showHostile || false,
              isBoss: false,
              showHealth: values.showHealth,
              showInitiative: values.showInitiative,
              showAC: values.showAC,
              showName: values.showName,
            }));
          }
        }
    >
      <Form>
        <HStack
          mt={4}
          mb={4}
        >
          {
              addFormFields.map(([label, key, IC]) => (
                <IC
                  hideError
                  name={key}
                  label={label}
                  key={key}
                />
              ))
            }
        </HStack>
        <AddNPCFlags />
        <Button
          mb={4}
          width="100%"
          type="submit"
          variant="solid"
          colorScheme="green"
          rightIcon={<Icon as={AddIcon} />}
        >
          Add
          {' '}
          {showHostile ? 'Enemy' : 'NPC'}
        </Button>
      </Form>
    </Formik>
  );
}
