import {
  Badge, Box, Button, Center, Checkbox, CheckboxGroup, Divider, Flex, Grid,
  Heading, HStack, Icon, IconButton, Input, NumberInput, Text,
  NumberInputField, Select, Slide, useCheckboxGroup, useDisclosure, VStack,
} from '@chakra-ui/react';
import React, { useRef, useState } from 'react';
import { AiOutlineLink } from 'react-icons/ai';
import { BsHeartFill } from 'react-icons/bs';
import { GiCheckedShield, GiDiceTwentyFacesTwenty, GiWalkingBoot } from 'react-icons/gi';
import { useHistory } from 'react-router-dom';
import { characterTypeColours } from '../App';
import { Character } from '../Characters/Character.interfaces';
import { NPC } from '../Characters/NPC.interfaces';
import { Player } from '../Characters/Player.interfaces';
import { setTurn } from '../common/campaignSlice';
import { useAppDispatch, useAppSelector } from '../common/hooks';
import { modifyNPC } from '../common/npcSlice';
import { modifyPlayer, rollAllPlayerInitiative } from '../common/playerSlice';
import { getSortedTurns } from '../Initiative';
import { DeathSaveCounter } from './DeathSaveCounter';

export function DMScreen() {
  const history = useHistory();

  const {
    value: selectedCharacters,
    setValue: setSelectedCharacters,
  } = useCheckboxGroup({
    defaultValue: [] as string[],
  });

  const changePageMethod = ({ type, id }: {
    type: string;
    id: string
  }): void => {
    history.push(`/${type}/${id}`);
    const maxAttempt = 20;
    const findFocus = (iter = 0) => {
      if (iter >= maxAttempt) return;
      const element = document.querySelector(`#currentHP-${id}`) as HTMLInputElement | null;

      if (!element) {
        setTimeout(() => {
          findFocus(iter + 1);
        }, 50);
      } else {
        element.focus();
      }
    };
    findFocus();
  };

  const {
    isOpen,
    onOpen,
    onClose,
  } = useDisclosure();

  const editPanelRef = useRef<HTMLDivElement>(null);

  const players = useAppSelector<Player[]>((state) => state.players.players);
  const npcs = useAppSelector<NPC[]>((state) => state.npcs.npcs);
  const currentTurnId = useAppSelector<string>((state) => state.campaign.currentTurnId);

  const characters = getSortedTurns({
    players,
    npcs,
    showAllTurns: false,
    currentTurnId,
  });

  const availablePlayers = characters.filter((i) => i.type === 'player').map((i) => i.character);
  const availableEnemies = characters.filter((i) => i.type === 'npc' && (i.character as NPC).isHostile).map((i) => i.character);

  const dispatch = useAppDispatch();

  const rollInitiativesAction = () => {
    dispatch(rollAllPlayerInitiative());
    dispatch(setTurn(''));
  };

  const setTurnAction = () => {
    if (characters.length < 1) {
      return;
    }

    if (!currentTurnId) {
      dispatch(setTurn(characters[0].character.id));
      return;
    }

    const currentTurnCharacter = characters.findIndex((i) => i.character.id === currentTurnId);

    if (!currentTurnCharacter) {
      dispatch(setTurn(characters[1].character.id));
      return;
    }
    const nextCharacter = characters[(currentTurnCharacter + 1) % characters.length];
    dispatch(setTurn(nextCharacter.character.id));
  };

  // Set Health/THP funcs
  const [healthMod, setHealthMod] = useState(0);
  const [thpMod, setThpMod] = useState(0);

  const hPModPanels: Array<{
    title: string;
    prop: 'currentHP' | 'tempHP';
    value: number;
    setMethod: (value: number) => void;
  }> = [
    {
      title: 'Modify Health',
      prop: 'currentHP',
      value: healthMod,
      setMethod: setHealthMod,
    },
    {
      title: 'Modify THP',
      prop: 'tempHP',
      value: thpMod,
      setMethod: setThpMod,
    },
  ];

  const modifyHPValue = (value: number, prop: 'currentHP' | 'tempHP', reset?: boolean) => {
    if (value === 0 && !reset) return;

    const dispatchActions: unknown[] = [];
    const selectedPlayers = players.filter((i) => selectedCharacters.includes(i.id));

    const resetProps = (character: Character) => ({
      id: character.id,
      tempHP: prop === 'tempHP' ? 0 : Math.max(character.tempHP, 0),
      currentHP: prop === 'currentHP' ? character.maxHP : character.currentHP,
    });

    const modProps = (character: Character) => ({
      id: character.id,
      [prop]: prop === 'tempHP'
        ? Math.max(character[prop] + value, 0)
        : Math.min(
          character[prop] + value,
          character.maxHP,
        ),
    });

    const propMethod = reset ? resetProps : modProps;

    selectedPlayers.forEach((player) => {
      dispatchActions.push(modifyPlayer(propMethod(player)));
    });

    const selectedNPCs = npcs.filter((i) => selectedCharacters.includes(i.id));
    selectedNPCs.forEach((npc) => {
      dispatchActions.push(modifyNPC(propMethod(npc)));
    });

    if (dispatchActions.length) dispatchActions.map(dispatch);
  };

  const allExistingConditions = [...players, ...npcs].reduce((p, v) => {
    if (v.conditions.length) {
      v.conditions.forEach((c) => {
        if (!p.includes(c)) p.push(c);
      });
    }
    return p;
  }, [] as string[]);

  const [conditionToRemove, setConditionToRemove] = useState('');
  const [conditionToAdd, setConditionToAdd] = useState('');

  const modifyCondValue = (value: string, action: 'remove' | 'add') => {
    if (!value) return;

    const dispatchActions: unknown[] = [];
    const selectedPlayers = players.filter((i) => selectedCharacters.includes(i.id));

    const modConditionValue = (conditions: string[]) => {
      const modifiedCondition = [...conditions];
      const conditionIndex = modifiedCondition.indexOf(value);
      const conditionAlreadyExists = conditionIndex > -1;
      if (action === 'remove' && conditionAlreadyExists) {
        modifiedCondition.splice(conditionIndex, 1);
      }

      if (action === 'add' && !conditionAlreadyExists) {
        modifiedCondition.push(value);
      }

      return modifiedCondition;
    };

    selectedPlayers.forEach((player) => {
      dispatchActions.push(modifyPlayer({
        id: player.id,
        conditions: modConditionValue(player.conditions),
      }));
    });

    const selectedNPCs = npcs.filter((i) => selectedCharacters.includes(i.id));
    selectedNPCs.forEach((npc) => {
      dispatchActions.push(modifyNPC({
        id: npc.id,
        conditions: modConditionValue(npc.conditions),
      }));
    });

    if (dispatchActions.length) dispatchActions.map(dispatch);
  };

  return (
    <Flex>
      <VStack width="100%">
        <HStack
          width="100%"
        >
          <Button
            flexGrow={1}
            disabled={selectedCharacters.length === 0}
            size="xs"
            onClick={() => setSelectedCharacters([])}
          >
            Deselect All Characters
          </Button>
          <Button
            flexGrow={1}
            onClick={() => setSelectedCharacters(availablePlayers.map((i) => i.id))}
            size="xs"
          >
            Select All Players
          </Button>
          <Button
            flexGrow={1}
            onClick={() => setSelectedCharacters(
              availableEnemies
                .map((i) => i.id),
            )}
            size="xs"
          >
            Select All Enemies
          </Button>
        </HStack>
        {/* Characters */}
        <VStack width="100%" spacing={1}>
          <CheckboxGroup
            defaultValue={[]}
            value={selectedCharacters}
            onChange={setSelectedCharacters}
          >
            {
              characters.map((i) => {
                const { character } = i;
                const npc = character as NPC;
                const player = character as Player;
                const tempHP = character.tempHP ? ` (${character.tempHP})` : '';
                let type: string = '';
                if (i.type === 'player') {
                  type = 'players';
                } else if (i.type === 'npc') {
                  type = npc.isHostile ? 'enemies' : 'npcs';
                }

                let borderColour: string = characterTypeColours.neutral;

                if (type === 'players') {
                  borderColour = characterTypeColours.player;
                } else if (type === 'enemies') {
                  borderColour = characterTypeColours.enemy;
                } else if (npc.isFriendly) {
                  borderColour = characterTypeColours.friendly;
                }
                return (
                  <Box
                    width="100%"
                    key={character.id}
                    border={`0.1em solid ${borderColour}`}
                    borderRadius={10}
                  >
                    {
                      character.conditions.map((c) => c.toLowerCase()).includes('dying')
                        && type === 'players' && (
                          <DeathSaveCounter player={player} size="xs" />
                      )
                    }
                    <Grid
                      key={character.id}
                      alignItems="center"
                      templateColumns="repeat(5, 1fr)"
                      fontSize="xs"
                      pl={2}
                      pr={2}
                      gap={1}
                    >
                      <HStack>
                        <Checkbox value={character.id} size="lg" />
                        {' '}
                        <Text
                          fontWeight="bold"
                          fontSize="xs"
                        >
                          {character.name}
                        </Text>
                      </HStack>
                      <Box>
                        <Icon as={GiCheckedShield} />
                        {' '}
                        {character.armourClass}
                      </Box>
                      <Box>
                        <Icon as={BsHeartFill} />
                        {' '}
                        {character.currentHP}
                        {' '}
                        {tempHP}
                        {' '}
                        /
                        {' '}
                        {character.maxHP}
                      </Box>
                      <Box>
                        <Icon as={GiWalkingBoot} />
                        {' '}
                        {character.initiative + character.initiativeMod}
                        {' '}
                        (
                        {character.initiative}
                        <Icon as={GiDiceTwentyFacesTwenty} />
                        {' '}
                        {character.initiativeMod >= 0 ? '+' : '-'}
                        {' '}
                        {Math.abs(character.initiativeMod)}
                        )
                      </Box>
                      <Box>
                        <IconButton
                          aria-label="Go to character"
                          icon={<Icon as={AiOutlineLink} />}
                          onClick={() => changePageMethod({
                            id: character.id,
                            type,
                          })}
                          size="xs"
                          m={1}
                        />
                      </Box>
                    </Grid>
                    {
                      character.conditions.length > 0 && (
                      <HStack pr={1} pl={1} pb={1}>
                        {character.conditions.map((cond) => (
                          <Badge variant="solid" key={cond}>{cond}</Badge>
                        ))}
                      </HStack>
                      )
                    }
                  </Box>
                );
              })
            }
          </CheckboxGroup>
        </VStack>
        {/* Spacer for edit panel height */}
        <Flex
          height={isOpen && editPanelRef.current ? `${editPanelRef.current.clientHeight}px` : 0}
        />

        {/* Controls */}
        <VStack
          width="100%"
          position="sticky"
          bottom="0"
          backgroundColor="white"
          p={2}
        >
          <Button width="100%" onClick={onOpen} display={isOpen ? 'none' : 'visible'}>
            Show Edit Panel
          </Button>

          {/* Edit Panel */}
          <Slide
            direction="bottom"
            in={isOpen}
            ref={editPanelRef}
          >
            <Center>
              <VStack
                p={2}
                minW="650"
                maxW="800"
                backgroundColor="white"
              >
                <Center><Heading size="md">Edit Panel</Heading></Center>
                <Divider />
                <Button
                  width="100%"
                  onClick={onClose}
                  size="xs"
                >
                  Hide This Panel
                </Button>
                <Divider />
                <HStack
                  backgroundColor="white"
                  width="100%"
                >
                  <Button onClick={rollInitiativesAction} width="100%">
                    Roll Initiative
                    <Icon as={GiDiceTwentyFacesTwenty} />
                  </Button>
                  <Button onClick={setTurnAction} width="100%">Next Turn</Button>
                </HStack>
                <Divider />
                <HStack
                  width="100%"
                >
                  {
                    hPModPanels.map(({
                      title, prop, value, setMethod,
                    }) => (
                      <VStack
                        width="50%"
                        key={title}
                      >
                        <Flex
                          flexGrow={1}
                        >
                          <Heading size="s">{title}</Heading>
                        </Flex>
                        <HStack>
                          {
                            [
                              -10,
                              -5,
                              -1,
                              1,
                              5,
                              10,
                            ].map((i) => (
                              <Button
                                size="xs"
                                onClick={() => modifyHPValue(i, prop)}
                                variant="outline"
                                disabled={!selectedCharacters.length}
                                key={i}
                              >
                                {i > 0 && '+'}
                                {i}
                              </Button>
                            ))
                          }
                        </HStack>
                        <HStack>
                          <Button
                            variant="outline"
                            disabled={!selectedCharacters.length}
                            onClick={() => modifyHPValue(-value, prop)}
                          >
                            -
                          </Button>
                          <NumberInput
                            value={value}
                            onChange={(e) => setMethod(Number(e) || 0)}
                            width="6em"
                            disabled={!selectedCharacters.length}
                          >
                            <NumberInputField />
                          </NumberInput>
                          <Button
                            variant="outline"
                            disabled={!selectedCharacters.length}
                            onClick={() => modifyHPValue(value, prop)}
                          >
                            +
                          </Button>
                        </HStack>
                        <Button
                          size="xs"
                          variant="outline"
                          disabled={!selectedCharacters.length}
                          onClick={() => modifyHPValue(0, prop, true)}
                        >
                          Reset
                        </Button>
                      </VStack>
                    ))
                  }
                </HStack>
                <Divider />
                <VStack width="100%">
                  <Heading size="s">
                    Modify Conditions
                  </Heading>
                  <HStack
                    width="100%"
                  >
                    <VStack
                      width="50%"
                    >
                      <Input
                        placeholder="Name of condition"
                        disabled={!selectedCharacters.length}
                        value={conditionToAdd}
                        onChange={(e) => setConditionToAdd(e.target.value)}
                      />
                      <Button
                        width="100%"
                        disabled={!selectedCharacters.length}
                        onClick={() => modifyCondValue(conditionToAdd, 'add')}
                      >
                        Add Condition
                      </Button>
                    </VStack>
                    <VStack
                      width="50%"
                    >
                      <Select
                        placeholder="Select condition to remove"
                        disabled={!selectedCharacters.length || !allExistingConditions.length}
                        value={conditionToRemove}
                        onChange={(e) => setConditionToRemove(e.target.value)}
                      >
                        {
                          allExistingConditions.map((c) => (
                            <option value={c} key={c}>{c}</option>
                          ))
                        }
                      </Select>
                      <Button
                        width="100%"
                        disabled={!selectedCharacters.length || !allExistingConditions.length}
                        onClick={() => modifyCondValue(conditionToRemove, 'remove')}
                      >
                        Remove Condition
                      </Button>
                    </VStack>
                  </HStack>
                </VStack>
              </VStack>
            </Center>
          </Slide>
          {/* End Edit Panel */}

        </VStack>
      </VStack>
    </Flex>
  );
}
